import React from 'react';
import './ForArtist_';
import './ExploreForm.css'
import ProfileOwnedCardItem from './ProfileOwnedCardItem'
import ProfileTokenCardItem from './ProfileTokenCardItem'
import { CardColumns, CardDeck } from 'react-bootstrap';

function ProfileForm (props) {

    let imageDescription;

    // console.log(props.music[0]);
    // if (props.music[0] != undefined) {
    //     console.log(props.music[0].contributor_1.royalty_perc);
    // }
    // console.log(props.permissions)
    // console.log(props.tokenAmount[0])
    return (
        <div>
        <div className='cards'>
        {/* <h1 >You've uploaded {props.ownedMusicCount} songs</h1> */}
        {props.madeMusicCount > 1 ? (<h1>My works</h1>) : (<h1>My work</h1>)}
        {/* <h1>My work</h1> */}
        <div className='cards__container'>
          <div className='cards__wrapper'>
            <ul className='cards__items'>
                { props.madeMusic.map((single, key) => {
                        return(
                            <ProfileOwnedCardItem
                            id={single.id}
                            // id={key+1}
                            title={single.title}
                            author_name={single.author_name}
                            track={single.track}
                            artwork={`https://ipfs.infura.io/ipfs/${single.artwork}`}
                            description={single.description}
                            cir_supply={single.cir_supply}
                            />
                        )
                    })}
            </ul>
          </div>
        </div>
      </div>
        <div className='cards'>
        {/* <h1 >and have {props.permitMusicCount} permission</h1> */}
        {props.ownedMusicCount > 1 ? (<h1>My tokens</h1>) : (<h1>My token</h1>)}
        {/* <h1 >My token</h1> */}
        <div className='cards__container'>
          <div className='cards__wrapper'>
            <ul className='cards__items'>
                { props.ownedMusic.map((single, key) => {
                        return(
                            <ProfileTokenCardItem
                            id={single.id}
                            // id={key+1}
                            title={single.title}
                            author_name={single.author_name}
                            track={single.track}
                            artwork={`https://ipfs.infura.io/ipfs/${single.artwork}`}
                            description={single.description}
                            // permissions={props.permissions}
                            // deadline={props.permissions[key].deadline}
                            // tokenamount={props.tokenAmount[key]}
                            tokenamount={single.tokenAmount}
                            cir_supply={single.cir_supply}
                            />
                        )
                    })}
            </ul>
          </div>
        </div>
      </div>
      {/* <SongItem/> */}
        </div>
    );
}

export default ProfileForm;



