import React, { useState, useEffect, useRef, useContext, useReducer } from 'react'
import { useStateValue } from "./StateProvider";
import PlayCircleOutlineIcon from "@material-ui/icons/PlayCircleOutline";
import SkipPreviousIcon from "@material-ui/icons/SkipPrevious";
import SkipNextIcon from "@material-ui/icons/SkipNext";
import ShuffleIcon from "@material-ui/icons/Shuffle";
import RepeatIcon from "@material-ui/icons/Repeat";
import VolumeDownIcon from "@material-ui/icons/VolumeDown";
import PauseCircleOutlineIcon from "@material-ui/icons/PauseCircleOutline";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistPlay";
import "./SinglePlayer.css";
import { Grid, Slider } from "@material-ui/core";
import PlayerState from './PlayerState'

import playerReducer from './PlayerReducer';

// function Footer({ spotify }) {
// function Footer({ props }) {
function Player(props) {
//   const [{ token, item, playing }, dispatch] = useStateValue();
    // const [playing, setPlaying] = useState(false);

  // Global State
    // const {
    //     // currentSong,
    //     // songs,
    //     // nextSong,
    //     // prevSong,
    //     // repeat,
    //     // random,
    //     // playing,
    //     // toggleRandom,
    //     // toggleRepeat,
    //     togglePlaying,
    //     // handleEnd,
    // } = useContext(playerContext)

    const initialState = {
        // currentSong: 0,
        // songs: songsArr,
        // repeat: false,
        // random: false,
        playing: false,
        audio: null
      }

    const TOGGLE_PLAYING = 'TOGGLE_PLAYING';
    const [state, dispatch] = useReducer(playerReducer, initialState);
    const [audio] = useState(new Audio(`https://ipfs.infura.io/ipfs/${props.track}`));
    const [playing, setPlaying] = useState(false);
    const [statevolum, setStateVolum] = useState(0.5);
    // const [dur, setDur] = useState(0)
    const [dur, setDur] = useState(0)
    // setDur(audio.duration)
    console.log(audio.duration)
    // console.log(dur)
    const [currentTime, setCurrentTime] = useState(50)
    // const [currentTime, setCurrentTime] = useState(0)
    const fmtMSS = (s) => { return (s - (s %= 60)) / 60 + (9 < s ? ':' : ':0') + ~~(s) }
    const toggleAudio = () => audio.paused ? audio.play() : audio.pause();
    const togglePlaying = () => dispatch({ type: TOGGLE_PLAYING, data: state.playing ? false : true })

    const toggle = () => setPlaying(!playing);

    const handleVolume = (q) => {
        setStateVolum(q);
        audio.volume = q;
      }

    const handleProgress = (e) => {
    // console.log(dur)
    let compute = (e.target.value * dur) / 100;
    setCurrentTime(compute);
    audio.currentTime = compute;
    // console.log(currentTime);
    }



    useEffect(() => {
        // audio.current.volume = statevolum;
        audio.volume = statevolum;
        playing ? audio.play() : audio.pause();
        // setDur(audio.duration)

      },
      [playing]
    );


  return (
    <PlayerState>
        <div className="footer">
        {/* <h1>{dur}</h1> */}
        <audio

        onTimeUpdate={(e) => setCurrentTime(e.target.currentTime)}
        // onCanPlay={(e) => setDur(e.target.duration)}
        // onCanPlay={(e) => setDur(50)}
        // onEnded={handleEnd}
        ref={audio}
        type="audio/mpeg"
        preload='true'
        // src={songs[currentSong][1]}
        />

        <div className="progressb">
            <span className="currentT">{fmtMSS(currentTime)}</span>
            <input
                onChange={handleProgress}
                value={dur ? (currentTime * 100) / dur : 0}
                type="range" name="progresBar" id="prgbar" />
            <span className="totalT">{fmtMSS(dur)}</span>
        </div>



        <div className="footer__left">
        </div>
        <div className="footer__center">

            {/* {playing ? ( */}
            {!audio.paused ? (
            <PauseCircleOutlineIcon
                // onClick={handlePlayPause}
                // onClick={toggle}
                onClick={() => { togglePlaying(); toggleAudio(); }}
                fontSize="large"
                className="footer__icon"
            />
            ) : (
            <PlayCircleOutlineIcon
                // onClick={handlePlayPause}
                // onClick={toggle}
                onClick={() => { togglePlaying(); toggleAudio(); }}
                fontSize="large"
                className="footer__icon"
            />
            )}
            {/* <SkipNextIcon onClick={skipPrevious} className="footer__icon" /> */}
        </div>
        <div className="footer__right">
            {/* <Grid container spacing={2}> */}
            <Grid container spacing={1}>
            {/* <Grid item>
                <PlaylistPlayIcon />
            </Grid> */}
            <Grid item>
                {/* <VolumeDownIcon /> */}
            </Grid>
            <Grid item xs>
                {/* <Slider aria-labelledby="continuous-slider" /> */}
                <input value={Math.round(statevolum * 100)} type="range" name="volBar" id="volBar" onChange={(e) => handleVolume(e.target.value / 100)} />
            </Grid>
            </Grid>
        </div>
        </div>
    </PlayerState>
  );
}

export default Player;
