import React, { useState, useEffect } from 'react';
import { Button } from './Button';
import { Link } from 'react-router-dom';
import Web3 from 'web3';
import './Navbar.css';
import Yald from '../abis/Yald.json'

function Navbar() {
    const [web3, setWeb3] = useState(undefined);
    const [accounts, setAccounts] = useState([]);
    const [balance, setBalance] = useState(0);

  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const [cbal, setCbal] = useState(0);
  const [yald, setYald] = useState([]);
//   const [test1, setTest1] = useState(0);
//   const [test2, setTest2] = useState(0);


  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();

    const init = async () => {
        try {
            if (window.ethereum) {
            window.web3 = new Web3(window.ethereum)
            await window.ethereum.enable()
            }
            else if (window.web3) {
            window.web3 = new Web3(window.web3.currentProvider)
            }
            else {
            window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
            }
            const web3 = window.web3
            setWeb3(web3)
            const accounts = await web3.eth.getAccounts()
            setAccounts(accounts)
            // setTest1(1)
            // setTest2(2)


            const networkId = await web3.eth.net.getId()
            // console.log(accounts[0])
            const networkData = Yald.networks[networkId]
            // console.log(networkData)
            if(networkData) {
                const yald = new web3.eth.Contract(Yald.abi, networkData.address)
                setYald(yald)
                const cbal = await yald.methods.contract_bal().call()
                // console.log(cbal.toString().localeCompare("0"))
                // string_a.localeCompare(string_b);
                if (!cbal.toString().localeCompare("0")) {
                    console.log(cbal.toString())
                    setCbal(cbal)
                } else {
                    // setCbal(Math.round(web3.utils.fromWei(cbal) * 1e3) / 1e3)
                    console.log(cbal.toString())
                    setCbal(Math.round(window.web3.utils.fromWei(cbal.toString()) * 1e2) / 1e2)
                    // setCbal(Math.round(web3.utils.fromWei(cbal.toString())))
                    // setCbal(cbal.toString())
                }


                // setMusicCount(musicCount)
                // // Load images
                // for (var i = 1; i <= musicCount; i++) {
                //     const single = await yald.methods.music(i).call()
                //     setMusic(music => [...music, single])
                // }
                // this.setState({ loading: false})
            } else {
                window.alert('Yald contract not deployed to detected network.')
            }


            if(typeof accounts[0]!== 'undefined'){
                const _balance = await web3.eth.getBalance(accounts[0])
                // this.setState({ account:accounts[0], balance:balance, web3:web3})
                setBalance(Math.round(web3.utils.fromWei(_balance) * 1e2) / 1e2)
            } else {
                window.alert('Please login with MetaMask')
            }
        }

    catch (error) {
        alert (error);
        // alert ('Failed to load web3, accounts, or contract. Check console for details.');
    }

    }
    init()

  }, []);

    window.addEventListener('resize', showButton);
    // setBalance(balance.toFixed(2))
  return (
    <>
      <nav className='navbar'>
        <div className='navbar-container'>
          <Link to='/' className='navbar-logo' onClick={closeMobileMenu}>
            <i class="fas fa-wave-square fa-flip-horizontal"></i>
          </Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            {/* <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Home
              </Link>
            </li> */}
            {/* <li className='nav-item'>
              <Link
                to='/services'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Services
              </Link>
            </li> */}

            {/* the balance of smart contractt
            <li className='nav-item'>
              <Link
                to=''
                className='nav-links'
                onClick={closeMobileMenu}
              >
                {cbal.toString()} ETH
              </Link>
            </li> */}



            <li className='nav-item'>
              <Link
                to=''
                className='nav-links'
                onClick={closeMobileMenu}
              >
                {balance} ETH
              </Link>
            </li>

            <li>
              <Link
                to='/sign-up'
                className='nav-links-mobile'
                onClick={closeMobileMenu}
              >
                {balance}
              </Link>
            </li>
          </ul>
          {button && <Button buttonStyle='btn--outline'>{accounts[0]}</Button>}
        </div>
      </nav>
    </>
  );
}

export default Navbar;
